import React, { useState } from 'react';
import styles from './tabs.module.scss';
import { Button, Form, FormGroup, Label, Input, Row, Col } from 'reactstrap';
import sendRequest from '../../utils/sendRequest';
import Swal from 'sweetalert';

function Individuals() {
    const [individualData, setData] = useState({});
    const addData = (e) => {
        setData({ ...individualData, [e.target.name]: e.target.value });
    };
    const sendData = async (e) => {
        e.preventDefault();
        const res = await sendRequest('signup', individualData, { 'user-type': 'individual' });
        if (res.status === 200) {
            document.getElementById('individualForm').reset();
            Swal('Good job!', res.data.message, 'success');            
        } else {
            Swal('Oops!', res.message, 'error');
        }
    };
    return (
        <section className={`${styles.form} py-5 px-5`}>
            <Form onSubmit={sendData} id='individualForm'>
                <Row>
                    <Col xs="12" md="12">
                        <h2 className="text-capitalize mb-5 text-dark">Individuals</h2>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">first name</Label>
                            <Input type="text" placeholder="First Name" name='firstName' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                        <FormGroup>
                            <Label className="text-capitalize">last name</Label>
                            <Input type="text" placeholder="Last Name" name='lastName' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">Email</Label>
                            <Input type="email" placeholder="Email" name='email' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <FormGroup>
                            <Label className="text-capitalize">phone number</Label>
                            <Input type="tel" placeholder="phone number" name='phone' onChange={addData} required />
                        </FormGroup>
                    </Col>
                    <Col xs="12" md="12">
                        <Button className="text-uppercase w-100" color="primary" type='submit'>
                            request
                        </Button>
                    </Col>
                </Row>
            </Form>
        </section>
    );
}

export default Individuals;
