import React, { useState } from 'react';

import styles from './navbar.module.scss';

import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink,
    Button,
} from 'reactstrap';

function BrandNavbar() {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => setIsOpen(!isOpen);
    return (
        <div className={`${styles.nav}`}>
            <Navbar expand="md" light className="py-4 px-0">
                <NavbarBrand href="/">avatar</NavbarBrand>
                <NavbarToggler onClick={toggle} />
                <Collapse isOpen={isOpen} navbar>
                    <Nav className={`${styles.navItem} ml-auto`} navbar>
                        <NavItem>
                            <NavLink href="#" className="text-uppercase text-primary">
                contact us
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink href="#" className="text-uppercase">
                                <img src="assets/egypt.svg" alt="lang" />
                            </NavLink>
                        </NavItem>
                        <NavItem className="pr-0">
                            <NavLink href="#" className="text-uppercase">
                                <Button outline color="primary" className="px-4 py-2">
                  get started
                                </Button>
                            </NavLink>
                        </NavItem>
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    );
}

export default BrandNavbar;
