import React, { useState } from 'react';
import {
    TabContent,
    TabPane,
    Nav,
    NavItem,
    NavLink,
    Row,
    Col,
} from 'reactstrap';
import styles from './tabs.module.scss';
import classnames from 'classnames';

import Individuals from './individuals';
import Companies from './companies';

function Tabs() {
    const [activeTab, setActiveTab] = useState('1');

    const toggle = (tab) => {
        if (activeTab !== tab) setActiveTab(tab);
    };
    return (
        <section id="avatar-tabs" className={`${styles.Tabs} py-5 my-5`}>
            <div className={`${styles.formNav} container-fluid bg-primary pt-5`}>
                <Nav tabs className="col-sm-12 col-md-8 mx-auto border-0 p-0">
                    <NavItem className={'col-sm-6 col-md-6 p-0'}>
                        <NavLink
                            className={`${classnames({
                                active: activeTab === '1',
                            })} py-4 text-center`}
                            onClick={() => {
                                toggle('1');
                            }}
                        >
              Individuals
                        </NavLink>
                    </NavItem>
                    <NavItem className={'col-sm-6 col-md-6 p-0'}>
                        <NavLink
                            className={`${classnames({
                                active: activeTab === '2',
                            })} py-4 text-center`}
                            onClick={() => {
                                toggle('2');
                            }}
                        >
              Companies
                        </NavLink>
                    </NavItem>
                </Nav>
            </div>
            <div className={`${styles.formArea} container-fluid`}>
                <Row className="justify-content-center">
                    <Col xs="12" md="8">
                        <TabContent activeTab={activeTab}>
                            <TabPane tabId="1">
                                <Row className="align-items-center">
                                    <Col xs="12" md="12" lg="8" xl="8" className="mb-5">
                                        <Individuals />
                                    </Col>
                                    <Col xs="12" md="12" lg="4" xl="4" className="text-center">
                                        <img
                                            className="img-fluid"
                                            src="https://via.placeholder.com/300x500"
                                            alt="al"
                                        />
                                    </Col>
                                </Row>
                            </TabPane>
                            <TabPane tabId="2">
                                <Row className="align-items-center">
                                    <Col xs="12" md="12" lg="8" xl="8" className="mb-5">
                                        <Companies />
                                    </Col>
                                    <Col xs="12" md="12" lg="4" xl="4" className="text-center">
                                        <img
                                            className="img-fluid"
                                            src="https://via.placeholder.com/300x500"
                                            alt="al"
                                        />
                                    </Col>
                                </Row>
                            </TabPane>
                        </TabContent>
                    </Col>
                </Row>
            </div>
        </section>
    );
}

export default Tabs;
